<template>
    <div>
        <!--  -->
        <div>
            <el-breadcrumb separator="/">
                <el-breadcrumb-item to="/index"></el-breadcrumb-item>
                <el-breadcrumb-item></el-breadcrumb-item>
                <el-breadcrumb-item></el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <el-card>
            <div>
                <h1 style="text-align: center;"></h1>
            </div>
            <el-table :data="subInfo" stripe border>
                <el-table-column prop="caseName" label="">
                </el-table-column>
                <el-table-column prop="subName" label="">
                </el-table-column>

                <el-table-column prop="level" label="">
                </el-table-column>

                <el-table-column prop="planDays" label="">
                </el-table-column>

                <el-table-column label="">
                    <template slot-scope="scope">
                        <el-tooltip effect="dark" content="" placement="top" :enterable="false"
                            v-if="user.type===1 && scope.row.startTime === null">
                            <el-button type="success" size="mini" icon="el-icon-video-play" round
                                @click="launch(scope.row)"></el-button>
                        </el-tooltip>
                        <el-tooltip class="item" effect="dark" content="" placement="right-start">
                            <el-button icon="el-icon-s-promotion" type="primary" size="mini" round
                                @click="openCaseDetail(scope.row)">
                            </el-button>
                        </el-tooltip>
                    </template>
                </el-table-column>
            </el-table>
        </el-card>

        <!--  -->
        <el-dialog title="" :visible.sync="setDirectorVisible" width="30%">
            <el-select v-model="directors" multiple filterable placeholder="">
                <el-option-group v-for="group in allUser" :key="group.value" :label="group.label">
                    <el-option v-for="item in group.children" :key="item.id" :label="item.name" :value="item.id">
                    </el-option>
                </el-option-group>
            </el-select>
            <span slot="footer" class="dialog-footer">
                <el-button @click="setDirectorVisible = false"> </el-button>
                <el-button type="danger" @click="launchSub(curCaseSub)"></el-button>
                <el-button type="primary" @click="setDirectorFn()"> </el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
import { exceptionSub } from '@/api/caseSub'
import { getUserListWithAssistants } from '@/api/user'
import { updateCaseSub, startOrFinish } from '@/api/caseSub'
import { formatDate } from '@/utils/common'
import { setDirector } from '@/api/caseSubUser'
import { mapState } from 'vuex'
export default {
    name:'checkException',
    data() {
        return {
            subInfo: [],
            setDirectorVisible: false,
            directors: [],
            curCaseSub: null,
            //
            allUser: [
                {
                    value: 0,
                    label: '',
                    children: []
                },
                {
                    value: 1,
                    label: '',
                    children: []
                },
                {
                    value: 2,
                    label: 'IE',
                    children: []
                }
            ],
        }
    },
    created() {
        this.exceptionSub()
        this.getAllUser()
    },
    computed:{
        ...mapState(['user'])
    },
    methods: {
        async exceptionSub() {
            const res = await exceptionSub()
            this.subInfo = res.data
        },
        //
        async launch(row) {
            this.initSetDirector(row.id)
        },
        //
        initSetDirector(id) {
            //
            this.setDirectorVisible = true
            //
            this.directors = null
            //id
            this.curCaseSub = id
        },
        //
        async getAllUser() {
            const res = await getUserListWithAssistants()
            if (res.code === 200) {
                this.editUser = res.data
                res.data.forEach((item) => {
                    this.allUser[item.status].children.push(item)
                })
            } else
                this.$message.error(res.msg)
        },
        // 
        async launchSub(id) {
            //
            var res = await startOrFinish({ id: id, startTime: formatDate(new Date()) + " 00:00:00" })
            if (res.code === 200) {
                this.$message.success(res.data)
                this.setDirectorVisible = false
                this.exceptionSub(this.caseId)
            } else {
                this.$message.error(res.msg)
            }
        },
        //
        async setDirectorFn() {
            const res = await setDirector({ id: this.curCaseSub, chargeId: this.directors })
            if (res.code === 200) {
                this.$message.success(res.data)
                //
                this.launchSub(this.curCaseSub)
            } else
                this.$message.error(res.msg)
        },
        //
        openCaseDetail(row){
            this.$router.push({
                name:'',
                query:{
                    caseId:row.caseId,
                    caseName:row.caseName
                }
            })
        }
    }
}
</script>

<style scoped></style>